// pages/index.js
import { useRef, useEffect, useState } from "react";
import Head from "next/head";

export default function StickyCardScroll() {
  const containerRef = useRef(null);
  const [activeIndex, setActiveIndex] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      if (!containerRef.current) return;

      const scrollY = window.scrollY;
      const containerTop = containerRef.current.offsetTop;
      const scrollPosition = scrollY - containerTop;

      // Calculate active index based on scroll position
      if (scrollPosition < 0) {
        setActiveIndex(0);
        return;
      }

      // Each card section is approximately 500px height
      const index = Math.min(3, Math.floor(scrollPosition / 500));
      setActiveIndex(index);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div className="container">
      <Head>
        <title>Sticky Card Scroll Animation</title>
        <meta
          name="description"
          content="Sticky card scroll animation effect"
        />
        <link rel="icon" href="/favicon.ico" />
      </Head>

      <header className="hero">
        <h1>Our Services</h1>
        <p>Scroll down to explore our offerings</p>
        <div className="scroll-indicator">↓</div>
      </header>

      <main ref={containerRef} className="card-container">
        <section
          className={`card-section ${activeIndex === 0 ? "active" : ""}`}
        >
          <div className="card">
            <div className="card-number">01</div>
            <h2>Web Design</h2>
            <p>
              Creating beautiful, responsive websites that work across all
              devices with modern UI/UX principles.
            </p>
            <div className="card-image">
              <div className="placeholder-image web-design"></div>
            </div>
          </div>
        </section>

        <section
          className={`card-section ${activeIndex === 1 ? "active" : ""}`}
        >
          <div className="card">
            <div className="card-number">02</div>
            <h2>Development</h2>
            <p>
              Building robust web applications with modern frameworks like
              Next.js, React, and Node.js.
            </p>
            <div className="card-image">
              <div className="placeholder-image development"></div>
            </div>
          </div>
        </section>

        <section
          className={`card-section ${activeIndex === 2 ? "active" : ""}`}
        >
          <div className="card">
            <div className="card-number">03</div>
            <h2>E-Commerce</h2>
            <p>
              Developing online stores with secure payment processing and
              intuitive shopping experiences.
            </p>
            <div className="card-image">
              <div className="placeholder-image ecommerce"></div>
            </div>
          </div>
        </section>

        <section
          className={`card-section ${activeIndex === 3 ? "active" : ""}`}
        >
          <div className="card">
            <div className="card-number">04</div>
            <h2>SEO Optimization</h2>
            <p>
              Improving your website's visibility on search engines with proven
              strategies and techniques.
            </p>
            <div className="card-image">
              <div className="placeholder-image seo"></div>
            </div>
          </div>
        </section>
      </main>

      <footer className="footer">
        <p>© 2023 Sticky Card Scroll Animation</p>
      </footer>

      <style jsx>{`
        * {
          margin: 0;
          padding: 0;
          box-sizing: border-box;
        }

        body {
          font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
            Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
          color: #333;
          overflow-x: hidden;
        }

        .container {
          min-height: 100vh;
        }

        .hero {
          height: 100vh;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          text-align: center;
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
          padding: 0 20px;
        }

        .hero h1 {
          font-size: 3.5rem;
          margin-bottom: 1rem;
        }

        .hero p {
          font-size: 1.2rem;
          max-width: 600px;
          margin-bottom: 2rem;
        }

        .scroll-indicator {
          font-size: 2.5rem;
          animation: bounce 2s infinite;
        }

        @keyframes bounce {
          0%,
          20%,
          50%,
          80%,
          100% {
            transform: translateY(0);
          }
          40% {
            transform: translateY(-20px);
          }
          60% {
            transform: translateY(-10px);
          }
        }

        .card-container {
          position: relative;
          margin: 0 auto;
          max-width: 1200px;
          padding: 0 20px;
        }

        .card-section {
          position: sticky;
          top: 0;
          height: 100vh;
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 100px 0;
          transition: opacity 0.3s ease;
        }

        .card-section:not(.active) {
          opacity: 0.3;
        }

        .card {
          background: white;
          border-radius: 20px;
          box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
          padding: 40px;
          max-width: 800px;
          width: 90%;
          transform-origin: center;
          transition: transform 0.5s ease, box-shadow 0.5s ease;
        }

        .card-section.active .card {
          transform: scale(1.05);
          box-shadow: 0 25px 70px rgba(0, 0, 0, 0.15);
        }

        .card-number {
          font-size: 5rem;
          font-weight: 800;
          color: rgba(102, 126, 234, 0.1);
          line-height: 1;
          margin-bottom: 1rem;
        }

        .card h2 {
          font-size: 2.5rem;
          margin-bottom: 1rem;
          color: #333;
        }

        .card p {
          font-size: 1.2rem;
          line-height: 1.6;
          margin-bottom: 2rem;
          color: #666;
        }

        .card-image {
          width: 100%;
          height: 300px;
          border-radius: 10px;
          overflow: hidden;
        }

        .placeholder-image {
          width: 100%;
          height: 100%;
          background: linear-gradient(45deg, #f3f4f6, #e5e7eb);
          display: flex;
          justify-content: center;
          align-items: center;
          color: #9ca3af;
          font-size: 1.5rem;
        }

        .web-design {
          background: linear-gradient(45deg, #ff9a9e, #fad0c4);
        }

        .development {
          background: linear-gradient(45deg, #a1c4fd, #c2e9fb);
        }

        .ecommerce {
          background: linear-gradient(45deg, #ffecd2, #fcb69f);
        }

        .seo {
          background: linear-gradient(45deg, #84fab0, #8fd3f4);
        }

        .footer {
          text-align: center;
          padding: 40px 20px;
          background: #f8f9fa;
          color: #666;
        }

        @media (max-width: 768px) {
          .hero h1 {
            font-size: 2.5rem;
          }

          .card {
            padding: 30px;
          }

          .card-number {
            font-size: 4rem;
          }

          .card h2 {
            font-size: 2rem;
          }
        }
      `}</style>
    </div>
  );
}
